








































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import CButton from '@/components/CButton.vue';
import CChobiGame from '@/components/links/CChobiGame.vue';
import FortuneData from '@/typescript/FortuneData.ts'
import MathUtil from '@/typescript/MathUtil.ts'
import StatisticsCalc from '@/typescript/StatisticsCalc.ts'
import moment from 'moment';

@Component({
  components: {
    CChobiGame,
    CButton,
  },
  head: {
    title: {
      inner: 'おみくじ',
    },
  },
})
export default class Fortune extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataButtonText: string = '占う';
  private dataFortune: {
    fortune: string,
    note: string,
    color: string,
    item: string,
    date: string,
  } = {
    fortune: '',
    note: '',
    color: '',
    item: '',
    date: '',
  };

  private created(): void {
    let localStrageFortune: any = localStorage.getItem('fortune');
    if (localStrageFortune !== null) {
      let fortune: {
        fortune: string,
        note: string,
        color: string,
        item: string,
        date: string,
      } = JSON.parse(localStrageFortune);
      console.log(fortune.date);
      if (fortune.date !== moment(new Date()).format('YYYYMMDD')) {
        localStorage.removeItem('fortune');
      } else {
        this.dataFortune = fortune;
      }
    }
  }

  // Methods
  private tellFortune(): void {
    let nowMoment: any = moment(new Date());
    if (nowMoment.format('YYYYMMDD') !== this.dataFortune.date) {
      Vue.set(this.dataFortune, 'date', nowMoment.format('YYYYMMDD'));
      Vue.set(this.dataFortune, 'note', FortuneData.notes[MathUtil.randomInt(0, FortuneData.notes.length - 1)]);
      Vue.set(this.dataFortune, 'color', FortuneData.colors[MathUtil.randomInt(0, FortuneData.colors.length - 1)]);
      Vue.set(this.dataFortune, 'item', FortuneData.items[MathUtil.randomInt(0, FortuneData.items.length - 1)]);
      let normalRandom: number = StatisticsCalc.generateNormalRandom(0, 1);
      if (normalRandom < -1.644854) {
        Vue.set(this.dataFortune, 'fortune', '大凶');
      } else if (normalRandom < -0.994458) {
        Vue.set(this.dataFortune, 'fortune', '凶');
      } else if (normalRandom < -0.318639) {
        Vue.set(this.dataFortune, 'fortune', '末吉');
      } else if (normalRandom <= 0.318639) {
        Vue.set(this.dataFortune, 'fortune', '小吉');
      } else if (normalRandom <= 0.994458) {
        Vue.set(this.dataFortune, 'fortune', '中吉');
      } else if (normalRandom <= 1.644854) {
        Vue.set(this.dataFortune, 'fortune', '吉');
      } else {
        Vue.set(this.dataFortune, 'fortune', '大吉');
      }
      localStorage.setItem('fortune', JSON.stringify(this.dataFortune));
    }
  }
}
